<template>
    <div class="leave-message" v-loading="submitDo">
        <tab-box :top-menu="topMenu" v-if="setup"></tab-box>
        <el-dialog
                :title="$L('添加留言')"
                :visible.sync="dialogVisible"
                width="30%"
                :close-on-click-modal="false"
                :modal-append-to-body='false' append-to-body class="crash-dialog">
            <el-input
                    type="textarea"
                    :placeholder=" $L('请输入留言内容...')"
                    v-model="textarea"
                    maxlength="500"
                    show-word-limit
                    rows="10">
            </el-input>
            <span slot="footer" class="dialog-footer">
                    <el-button @click="clearFunc" size="small">{{ $L('清空内容') }}</el-button>
                    <el-button type="primary" @click="submitFunc" size="small">{{ $L('提交留言') }}</el-button>
                </span>
        </el-dialog>
    </div>
</template>

<script>
    import tabBox from "../../components/layout/web/tabBox";

    export default {
        name: "leaveMessage",
        components: {
            tabBox
        },
        data() {
            return {
                topMenu: {
                    'buttonList': [
                        {
                            text: this.$L('添加留言'),
                            func: () => {
                                this.dialogVisible = true;
                            }
                        }
                    ],
                    'menuList': [
                        {
                            text:  this.$L('留言板'),
                            router: 'leaveList',
                        },
                    ]
                },
                dialogVisible: false,
                textarea: '',
                setup: true,
                submitDo:false
            }
        },
        methods: {
            clearFunc() {
                this.textarea = '';
            },
            reload(msg) {
                this.textarea = '';
                this.setup = false;
                this.$nextTick(() => {
                    this.setup = true;
                    this.submitDo = false;
                    if (msg && msg.hasOwnProperty('msg') && msg.msg !== '') {
                        this.$message({
                            type: msg.hasOwnProperty('type') ? msg.type : "success",
                            message: msg.msg
                        });
                    }
                })
            },
            submitFunc() {
                const _self = this;
                // AddLeaveMsg
                if (_self.textarea !== '') {
                    //关闭对话框并开启动画
                    _self.dialogVisible = false;
                    _self.submitDo = true;

                    _self.$api('AddLeaveMsg', {
                        content: _self.textarea
                    }, function (reData) {
                        if (reData.flag === 1) {
                            _self.reload({
                                type: "success",
                                msg: _self.$L("留言成功")
                            });
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.dialogVisible = true;
                                _self.submitDo = false;
                            }).catch(action => {
                                _self.$router.push({name: 'home'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.dialogVisible = true;
                            _self.submitDo = false;
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                } else {
                    _self.$message({
                        type: 'info',
                        message: _self.$L('未输入留言信息!')
                    });
                }

            },
        }

    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/common";

    .leave-message {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
    }

</style>