<template>
    <div class="tab-head">
        <div class="left" v-if="topMenu && topMenu.hasOwnProperty('menuList')">
            <div class="tab-item" v-for="item in topMenu.menuList"
                 :class="{ 'mainFontColor current mainBorderColor':checkActive(item)}"
                 v-if="!item.hasOwnProperty('hide') || item.hide===false || $route.name === item.router"
                 @click="gotoRouter(item)">{{ item.text }}
            </div>
        </div>
        <div class="left-txt mainBorderColor"
             v-if="topMenu && topMenu.hasOwnProperty('text') && topMenu.text.hasOwnProperty('val')"
             :class="{ border:topMenu.text.hasOwnProperty('border') && topMenu.text.border }">{{ topMenu.text.val }}
        </div>

        <div class="right" v-if="topMenu && topMenu.hasOwnProperty('buttonList')">
            <el-button
                    class="btn"
                    type="primary"
                    size="small"
                    v-if="$route.name !== item.router"
                    @click="gotoRouter(item)"
                    v-for="(item,index) in topMenu.buttonList"
                    :key="index">{{ item.text }}
            </el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "tabHead",
        props: {
            topMenu: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        methods: {
            checkActive(item) {
                if (item.hasOwnProperty('router') && this.$route.name !== item.router) {
                    if (!item.hasOwnProperty('checkRouter') || !this.$in_array(this.$route.name, item.checkRouter)) {
                        return false;
                    }
                }
                if (item.hasOwnProperty('params') && !this.$compareObj(item.params, this.$route.params)) {
                    return false;
                }
                return true;
            },
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                } else if (item && item.hasOwnProperty('params') || item && item.hasOwnProperty('paramsData')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({params: params});
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/lib";

    .tab-head {
        position: absolute;
        width: 100%;
        height: 50px;
        line-height: 50px;
        background: #fff;
        border-bottom: 1px solid #eff1f5;
        top: 0;
        left: 0;
        z-index: 100;
        overflow: hidden;
        font-size: 14px;
        color: #909399;
        @extend .no-select;

        .left {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;

            .tab-item {
                position: relative;
                padding: 0 20px;
                height: 100%;
                float: left;
                cursor: pointer;
                @include diy-animation-show(all);
                border-bottom: 2px solid #ffffff;

                &:hover {
                    color: #303133;
                }
            }
        }

        .left-txt {
            position: absolute;
            left: 20px;
            top: 0;
            height: 100%;
            font-size: 14px;

            &.border {
                top: 50%;
                height: 18px;
                line-height: 18px;
                transform: translateY(-50%);
                padding-left: 5px;
                border-left: 3px solid;
            }
        }

        .right {
            position: absolute;
            right: 10px;
            top: 0;
            height: 100%;

            .btn {
                @include diy-animation-show(all);
                margin: 0 10px 0 0;
                padding: 9px 20px;
            }
        }

    }
</style>